  @media(max-width:768px){
    .content{
      position: relative;
  
      top:3rem;
      z-index: 10;
      align-items: center;

      display: flex;
      overflow-y: hidden;
      scroll-behavior: smooth;
    }
    .content-block{
      height:40em;
      padding:2em;
      padding-top: 0;
      padding-bottom: 0;
      border-radius: 2em;
      margin: 5vw;
      margin-top: 0;
      width:100vw;

      position: relative;
      
      z-index: 10;
      display: grid;
      grid-template-rows: 20em 1fr;
      scroll-margin-left: 20em;
      scroll-margin-right: 20em;

      background-color: rgba(250, 235, 215, 0);
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
      border: 0.3em solid white;
      scroll-snap-align: start;

    }
    .image{
      height: 30vh;
      width:65vw;
      object-fit: cover;
      overflow: hidden;
      border-radius: 1em;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
    }
    .image img {
      width:150%;
      bottom: 0;
      transform:translateY(-6vh);
    }  
    
    .navbar-background,
    body::before {
      height: 12em;
      width:45%;
      bottom:-100px;
      right: 10%;
      position: fixed;
    }

    .text{
      width:70%;
      margin:15%;
      margin-top: 0;
      text-align:justify;
      overflow: hidden;
    }


  }
  
 



  
  .title{
    width:100%;
    margin-top: max(10px, 1vh);
    text-align: center;
  }



  
         
  .navbar-background{

    background-color:rgba(240, 240, 240, 0.9);
    z-index: 15;
    position: fixed;

    border-radius: 0.4em;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.13);
  }



  body::before {
    content: "";
    backdrop-filter: blur(2px); /* Adjust blur amount as needed */
    z-index: 10; /* Make sure this is below the .navbar-background */
  }

